.to-do-start-page {
  display: none;

  color: $color-basic;

  @include link;

  @media (min-width: $desktop-width) {
    display: block;

    &:hover,
    &:active {
      color: $color-link-hover;

      outline: none;
    }

    &:focus {
      outline: 1px solid $color-accent;
    }
  }
}
